body {
  margin: 0;
  font-family: "Chakra Petch", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white !important;
  background-color: transparent;
  overflow: hidden;
}

* {
  box-sizing: border-box;
  user-select: none !important;
}

.crt {
  background-image: linear-gradient(rgba(53, 17, 17, 0.1),
      rgba(31, 52, 78, 0.432));
  background-repeat: repeat;
  background-size: 100% 24px;
  animation: bgscroll 2s linear infinite;
}

@keyframes bgscroll {
  100% {
    background-position: 0% 100%;
  }
}

::selection {
  background: rgba(var(--color-primary), 1);
}

.index_terminal__teubZ,
.index_curvedTop__LX9XS {
  border-radius: 0 !important;
}

:root {
  --contexify-menu-bgColor: rgba(20, 20, 20, 0.98) !important;
  --contexify-separator-color: #4c4c4c !important;
  --contexify-item-color: #fff !important;
  --contexify-activeItem-color: #141414 !important;
  --contexify-activeItem-bgColor: rgba(var(--color-primary), 1) !important;
  --contexify-rightSlot-color: #6f6e77 !important;
  --contexify-activeRightSlot-color: #454545 !important;
  --contexify-arrow-color: #6f6e77 !important;
  --contexify-activeArrow-color: #2e2e2e !important;
  --contexify-zIndex: 9999 !important;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.header-buttons {
  background: #E81131;
  border: 0.8625px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0px 0px 41.4px rgba(232, 17, 49, 0.95);
}

.settings-item-indicator {
  background: rgba(109, 109, 109, 0.81);
  filter: blur(30px);
}

.status-gradient {
  display: flow-root;
  position: relative;
}

.status-gradient::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  border: 3px solid transparent;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.status-gradient.box-gradient::before {
  border-radius: 6px;
}

.status-icon-gradient {

  path {
    box-shadow: 0 0 0 4px #fff
  }
}

$hexagonSize: 38px;

.hexagon:before,
.hexagon:after {
  box-sizing: border-box;
  content: "";
  position: absolute;
  z-index: 1;
  width: $hexagonSize;
  height: $hexagonSize;
  transform: scaleY(0.5774) rotate(-45deg);
}

.hexagon:before {
  top: calc(-100% + 12px);
  border-top: solid 3px #333333;
  border-right: solid 3px #333333;
  background: transparent;
}

.hexagon:after {
  bottom: calc(-100% + 12px);
  border-bottom: solid 3px #333333;
  border-left: solid 3px #333333;
  background: transparent;
}

$hexagonSecondSize: 38px;

.hexagon-2th:before,
.hexagon-2th:after {
  box-sizing: border-box;
  content: "";
  position: absolute;
  z-index: 1;
  width: $hexagonSize;
  height: $hexagonSize;
  transform: scaleY(0.5774) rotate(-45deg);
}

.hexagon-2th:before {
  top: -9px;
  border-top: solid 7px #333333;
  border-right: solid 7px #333333;
  background: transparent;
}

.hexagon-2th:after {
  bottom: -9px;
  border-bottom: solid 7px #333333;
  border-left: solid 7px #333333;
  background: transparent;
}

.settings-item-content {
  * {
    margin: 0 !important;
  }
}

.cubic-text-bg {
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    width: 5%;
    height: 60%;
    margin: auto;
    margin-left: 0;
  }

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    width: 5%;
    height: 60%;
    margin: auto;
    margin-right: 0;
  }
}


.speedometer-inset-shadow {
  .chakra-text {
    background-color: #5CDCF9;
    color: transparent;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
  }
}

.menuTopItem {
  background: linear-gradient(89.97deg, rgba(9, 9, 9, 0.74) 6.79%, rgba(22, 22, 22, 0.74) 53.56%, rgba(11, 11, 11, 0.74) 96.72%);
  border: 0.808081px solid rgba(255, 255, 255, 0.1);
  border-radius: 3.31381px;
}